<template>
  <div class="root">
    <div v-if="loaded" class="carousel">
      <splide :options="settings">
        <splide-slide
          v-for="(item, index) in newsContent"
          :key="index"
        >
          <div class="item">
            <news-card
              :img="item.img"
              :text="item.text"
              :type="item.type"
              :pressUrl="item.pressUrl"

              class="custom-font"
            >
            <span :class="['news-title', item.isLatest ? 'latest' : '']">
  {{ item.isLatest ? 'LATEST NEWS' : 'NEWS' }}
</span>

            </news-card>
          </div>
        </splide-slide>
      </splide>
    </div>
  </div>
</template>


<style lang="scss" scoped>
@import './news-event-style.scss';
</style>

<script>
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import imgAPI from '@/assets/images/imgAPI';
import NewsCard from '../Cards/News';


const newsContent = [
  {
    text: 'Melissa Gorga Says She Would Be OK Leaving RHONJ If Bravo Goes a Certain Way',
    img: imgAPI.photo[1],
    type: 'caption_news',
    isLatest: true,  // Mark this as the latest news
    pressUrl: "https://www.hollywoodreporter.com/tv/tv-news/rhonj-melissa-gorga-teresa-giudice-season-15-1235983404/"
  },
  {
    text: 'Bravo Unveils New Shows & Renewals Including Chrissy...',
    img: imgAPI.photo[2],
    type: 'caption_news',
    isLatest: false,  // Mark this as regular news
    pressUrl: "https://www.bravotv.com/the-daily-dish/bravo-new-shows-making-it-in-manhattan-on-safari"
  },

];

export default {
  components: {
    NewsCard,
    Splide,
    SplideSlide,
  },
  data() {
    return {
      loaded: false,
      newsContent,
      settings: {
        pagination: true,
        type: 'loop',
        speed: 500,
        perPage: 2,
        perMove: 1,
        arrows: false,
        autoplay: true,
        interval: 5000,
        direction: 'ltr',
        reducedMotion: {
          speed: 500,
          rewindSpeed: 1000,
          autoplay: true,
        },
        breakpoints: {
          700: {
            perPage: 1,
          },
        },
      },
    };
  },
  mounted() {
    this.loaded = true;
    setTimeout(() => {
      if (this.$vuetify.locale.isRtl) {
        this.settings.direction = 'rtl';
      } else {
        this.settings.direction = 'ltr';
      }
    }, 100);
